.App {
  text-align: center;
  font-family: 'Rubik', Arial, sans-serif;
  background: linear-gradient(135deg, #667eea, #764ba2);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow-y: hidden;
  overflow-x: hidden;
}

/* Header Styling */
.App-header {
  background: linear-gradient(135deg, #ff9a9e, #fad0c4);
  padding: 20px 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: #fff;
  border-radius: 15px;
  margin-top: 100px;
  width: 90%;
  max-width: 800px;
}

.App-header h1 {
  margin: 0;
  font-size: 2.5em;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.App-header h2 {
  margin: 5px 0 0 0;
  font-size: 1.5em;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

/* Responsive Design for Mobile Devices */
@media (max-width: 768px) {
  .App-header {
    padding: 10px 5px;
  }

  .App-header h1 {
    font-size: 2em;
  }

  .App-header h2 {
    font-size: 1.2em;
  }
}

@media (max-width: 480px) {
  .App-header h1 {
    font-size: 1.5em;
  }

  .App-header h2 {
    font-size: 1em;
  }
}
